import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, SCP } from '../../../../models/dtos/mcg/scp';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface SCPEditViewProps extends EditViewProps<SCP> {}

const SCPEditView: FC<SCPEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const formProps = useForm<SCP>({ defaultValues: selectedItem, mode: 'onBlur' });

  const defaultWidth: number = 100;

  useEffect(
    () => {
      const {
        empresaMCG: { id },
      } = globalReducer.globalParameter;

      setTimeout(() => {
        formProps.reset({
          ...selectedItem,
          empresaMCG: {
            id,
          },
        });
      }, 500);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent title="Manutenção de Sociedades em Conta de Participação (SCP)">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline formProps={formProps} id="empresaMCG.id" hidden={true} />
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="cnpj"
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              label="CNPJ"
              labelWidth={defaultWidth}
              placeholder="Informe o CNPJ da Sociedade em Conta de Participação."
              type={InputType.CNPJ}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              labelWidth={defaultWidth}
              placeholder="Informe a Descrição da Sociedade em Conta de Participação."
              type={InputType.TEXT_AREA}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default SCPEditView;
